export default {
	data() {
		return {
			 providerList: []
		}
	},
	onLoad() {
		this.icon_login();
	},
	methods:{
		icon_login(){//点击图标登录
			uni.getProvider({
			    service: 'oauth',
			    success: (result) => {
			        this.providerList = result.provider.map((value) => {
			            let providerName = '';
						let icon='';
						let clss='';
			            switch (value) {
			                case 'weixin':
			                    providerName = '微信登录';
								icon="weixin";
								clss="weixin";
			                    break;
			                case 'qq':
			                    providerName = 'QQ登录';
								icon="QQ";
								clss="qq";
			                    break;
			                case 'sinaweibo':
			                    providerName = '新浪微博登录';
								icon="xinlangweibo";
								clss="xinlangweibo";
			                    break;
			                case 'alipay':
			                    providerName = '支付宝登录';
								icon="";
								clss="";
			                    break;
			            }
			            return {
			                name: providerName,
			                id: value,
							icon:icon,
							clss:clss
			            }
			        });
			
			    },
			    fail: (error) => {
			        console.log('获取登录通道失败', error);
			    }
			});
		},
		tologin(provider) {//点击登录
			uni.login({
				provider: provider.id,



				success: (res) => {
					console.log('login success:', res);
					// 更新保存在 store 中的登录状态
					console.log('登录保存到本地')
				},
				fail: (err) => {
					console.log('login fail:', err);
				}
			});
		}
	}
}
